$main_color: #1eacc7;
$second_color: #ff5656;

@mixin btn_hover {
    &:hover {
        background-image: none;
        background-color: $main_color; } }
* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative; }

nav {
    height: 50px;
    background-color: #212121; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style: none; }

.menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

.menu_item {
    border-right: 2px solid black;
    padding-right: 28px;

    &:nth-child(6) {
        border-right: none; } }

.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400; }
    &_call {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        margin-top: 19px;
        text-align: right; }
    &_phone {
        display: block;
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }
    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url('../img/bg/main_bg.jpg') center (center / cover) no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        margin: 0;
        color: #ffffff;
        font-size: 60px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 60px;
        text-align: center; }

    &_subheader {
        margin: 0;
        margin-top: 22px;
        color: #ffffff;
        font-size: 36px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 36px;
        text-align: center; }

    &_descr {
        margin-top: 23px;
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center; }

    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.reasons {
    padding: 71px 0 65px 0; }

.label {
    position: relative;
    z-index: 2;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    &_white {
        background-color: #fff;
        color: #000; } }

.title {
    position: relative;
    z-index: 2;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    &_white {
        color: #fff; } }

.subtitle {
    position: relative;
    z-index: 2;
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    &_white {
        color: #fff; } }

.reasons .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

.reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: $main_color;
    border-radius: 100%; }

.reasons_descr {
    width: 343px;

    .reasons_subtitle {
        line-height: 21px;
        color: $main_color;
        font-size: 17px;
        font-weight: 400; }

    .reasons_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_bg.png') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/bg/mobile_1.jpg') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/bg/mobile_2.jpg') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/bg/mobile_3.jpg') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/bg/mobile_4.jpg') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/bg/mobile_5.jpg') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/bg/mobile_6.jpg') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer;
            &:before {
                content: "";
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main_color;
                transition: 0.3s all; }
            &:after {
                content: "";
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                width: 2px;
                height: 14px;
                background-color: $main_color;
                transition: 0.3s all; }
            &:hover {
                &:before,&:after {
                    background-color: $second_color; } } } } }
.choice {
    position: relative;
    min-height: 895px;
    padding: 57px 0 138px 0;
    &_img {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 2; }
    &_descr {
        text-align: center;
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px; }
    .black {
        position: absolute;
        top: 0;
        left: 0;
        height: 499px;
        width: 100%;
        background-color: #070716;
        z-index: 1; } }

.require {
    padding: 68px 0 94px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        color: #1ac8c3;
        font-size: 22px;
        font-weight: 400; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            // display: inline-block
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &::before {
                content: "";
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                // transform: translateY(-50%)
                // vertical-align: middle
                // transform: translate(-5px,-2px)
                width: 21px;
                height: 20px;
                background: url('../icons/check_blue.svg') center (center / cover) no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: #1ac8c3; }

    .warning {
        .require_title {
            color: $second_color; }
        .require_list {
            li {
                &::before {
                     background: url('../icons/check_red.svg') center (center / cover) no-repeat; } } } } }
.world {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url('../img/bg/map.jpg') center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }
footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } }

	&_mobile {} }
